<template>
  <div v-show="isVisible" ref="publicProcessing" class="page">
    <div ref="coimheight" style="height:660px;z-index=189">
      <cube-scroll
        ref="scroll"
        class="horizontal-scroll-list-wrap"
        style="z-index:100;"
      >
        <cube-form :model="model">
          <cube-form-group>
            <cube-form-item v-for="(item,key) in fields" :key="key" :field="item" class="formfontstyle" :class="item.class"></cube-form-item>
          </cube-form-group>
          <div class="buttondiv">
            <button @click="finsh()">{{ fields[fields.length-1].buttonname }}</button>
          </div>
        </cube-form>
      </cube-scroll>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import visibilityMixin from "@/common/mixins/visibility"
import headtitle from "../headTitle/headTitle";  // eslint-disable-line 
import testinput from "@/components/form/testinput/test"     // eslint-disable-line 
import Progress from "../progress" // eslint-disable-line 
var moment = require("moment"); // 

// form 2 model  fields   model  fields    
export default {
  components: {
    // headtitle,
    Progress, // eslint-disable-line 
  },
  mixins: [visibilityMixin],
  props: {
    model: {
      type: Object,
      default () {
        return {
          disposaltime: "",
          disposalobeject: "",
          describe: "",
          uploadValue: [],
          textareaValue: "",
          note: "",
          test: ""
        }
      }
    },
    routerFrom: {
      type: String,
      default: "/"
    },
    //   
    fields: {
      type: Array,
      default() {
        return [
          {
            component: Progress,
            modelKey: "test",
            rules: {
              required: true
            },
            messages: {
              required: ""
            }
          },
          {
            type: "input",
            modelKey: "disposaltime",
            label: "",
            props: {
              placeholder: ""
            },
            rules: {
              required: true,
              notWhitespace: true
            }
          },
          {
            type: "input",
            modelKey: "disposalobeject",
            label: "",
            props: {
              placeholder: ""
            },
            rules: {
              required: true
            }
          },
          {
            type: "input",
            modelKey: "describe",
            label: "",
            props: {
              placeholder: ""
            },
            rules: {
              required: true
            }
          },
          {
            type: "textarea",
            modelKey: "textareaValue",
            label: "",
            props: {
              placeholder: ""
            },
            rules: {
              required: true
            }
          },
          {
            type: "upload",
            modelKey: "uploadValue",
            label: "",
            props: {
              placeholder: ""
            },
            rules: {
              required: true
            }
          },
          {
            type: "textarea",
            modelKey: "note",
            label: "",
            props: {
              maxlength: 160,
              placeholder: ""
            },
            rules: {
              required: true
            }
          },
          {
            type: "input",
            headtitlename: "",
            buttonname: ""
          }
        ]
      }

    }
  },
  data() {
    return {
      validity: {},
      isVisible: true,
      valid: undefined
    }
  },
  created() {

  },
  mounted() {
    this.longheights()
  },
  methods: {
    finsh() {
      this.showToastTime()
    },
    //  
    showToastTime() {
      const toast = this.$createToast({
        type: "correct",
        time: 1700,
        txt: "",
        onTimeout: () => {
          this.$router.replace(this.routerFrom)
          this.$emit("submitSuccess", this.model)
        }
      })
      toast.show()
    },

    longheights() {
      //   78% 
      const stillheight = window.screen.height * (0.9) + "px"
      // console.log(stillheight)
      this.$refs.coimheight.style.height = stillheight
      // console.log(this.$refs.coimheight.style.height, this.$parent.$refs.tabslist, stillheight)
      // console.log(this.$parent, this.$parent.$refs.tabslist.style.display, moment().format("YYYY-MM-DD hh:mm"))
      const newtime = moment().format("YYYY-MM-DD hh:mm")
      this.model.disposaltime = newtime
      // moment(time).format("YYYY-MM-DD hh:mm")
    }
  }
}

</script>

<style scoped lang="less">
.page{
  width: 100%;
  height: 100%;
  min-height: 100%;
  position: relative;
  background: #fff;
  z-index: 140 !important;
  .cube-form{
    z-index: 140;
    height: 100%;
 .buttondiv{
   margin-top:40px;
button{

    outline:none;
    width:320.66px;
    height:46px;
    line-height: 46px;
    font-size: 16.66px;
    margin:0 20px;
    border-radius: 23.33px;
    color:#fff;
    background:linear-gradient(to left,#004EFF,#34AEFB);
    border: 0px;
  }
  }
}
}
</style>
